$font-family: 'Gilroy', sans-serif;
$color-brand:#FF4419;

a, p, h1, h2, h3, h4, h5, h6, li, .h1,.h2, .h3, .h4, .h5, .h6{
    font-family: 'Gilroy', sans-serif !important;
} 

//Botones
@import "./botones.scss";

// Men√∫  //
.menu-top{
    border-bottom: 1px solid $color-brand;
    .logo-yaguar{
        max-height: 51px;
        width: auto;  
        @media(max-width:991px){
            max-height:40px;
            padding: 5px;
        }      
    }
    .nav-link{
        font-family: $font-family;
        font-size: 14px;
        font-weight: medium;
    }
    &-collapse{
        &.navbar-collapse{
            flex-grow: unset;
        }
    }
    .collapse-hidden{
        @media(max-width:991px){
            &_pc{
                display: block !important;
            }
            &_movil{
                display: none !important;
            }
        }
        @media(min-width:992px){
            &_pc{
                display: none !important;
            }
            &_movil{
                display: flex !important;
            }
        }

    }
} 

header{
    .py-header{
        padding: 100px 0 ;
    }
    .header{
        &-titulo{
            font-size: 60px;
            font-weight: bold;
            line-height: 68px;
            font-family: $font-family !important;
            text-align: left;
        }
        &-descripcion{
            font-size: 20px;
            font-family: $font-family;
        }
        &-btn{
            width: 198px;
            font-family: $font-family;
            font-size: 16px;
        }
        &-imagen{
            width:100%;    
            &-hidden_pc{
                display: none;
            } 
            &-hidden_movil{
                display: block;
            }            
        }
        
        @media(max-width:991px){
            &-titulo{
                font-size: 38px;
                font-weight: bold;
                line-height: 48px;
                font-family: $font-family !important;
                text-align: center;
            }
            &-descripcion{
                font-size: 16px;
                font-family: $font-family;
                text-align: center;
            }
            &-btn{
                width: 100%;
                font-family: $font-family;
                font-size: 16px;
            }
            &-imagen{
                width: 98%;    
                &-hidden_movil{
                    display: none;
                }    
                &-hidden_pc{
                    display: block;
                    padding: 25px 0;
                }      
            }
            
         
        }
    }
}

//Footer
footer{
    width: 100%;
    border-top: 1px solid $color-brand;
    padding-bottom: 20px;
    .footer{   
        display: flex;
        justify-content: space-between;
        flex-direction:row;
        ul{
            display: flex;
            flex-direction: column;
        }
        li{
            list-style: none;
        }
    }
    @media(max-width:991px){
        .footer{     
            justify-content: end;
            flex-direction:column;  
            ul{
                display: flex;
                flex-direction: row;
            }
            li{
                list-style: none;
            }
        }

    }
}
